<template>
	<div class="case-wrapper">
		<img :src="`../img/portfolio/${$route.params.slug}.png`" :alt="$route.params.slug">
		<div class="description">
			<h1>
				{{$route.params.item.title}}
				<span class="external-link" v-if="$route.params.item.link" :title="$route.params.item.link" @click.prevent="redirect($route.params.item.link)">
					<i class="fa fa-external-link"></i>
				</span>
			</h1>
			<p>{{$route.params}}</p>
		</div>
	</div>
</template>

<script>
export default {
	beforeRouteEnter(to, from, next) {
		next()
		document.querySelector('#app').classList.add('toCase')
	},
	beforeRouteLeave(to, from, next) {
		document.querySelector('#app').classList.remove('toCase')
		document.querySelector('#app').classList.add('fromCase')
		setTimeout(() => {
			document.querySelector('#app').classList.remove('fromCase')
			next()
		}, 280)
	},
	mounted() {
		window.scrollTo(0, 0)
		document.getElementsByTagName('body')[0].classList = this.$route.params.slug
	},
	destroyed() {
		document.getElementsByTagName('body')[0].removeAttribute('class')
	},
	methods: {
		redirect(link) {
			window.open(link)
		}
	}
}
</script>

<style lang="sass">
.case-wrapper
	min-height: 100vh
	padding-bottom: 5rem
	img
		margin-top: 1.5rem
		margin-bottom: 3rem
	.description
</style>
